import { Component, OnInit } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { VideoService } from '../services/video.service';

@Component({
  selector: 'app-videos',
  templateUrl: './videos.component.html',
  styleUrls: ['./videos.component.css']
})
export class VideosComponent implements OnInit {
  page: number = 1;
  currentVideos: string[];

  constructor(private videos: VideoService, private sanitizer: DomSanitizer) { }

  ngOnInit(): void {
    this.videos.all().subscribe(videos => {
      this.currentVideos = videos;
    });
  }

  generateUrl(url: string): SafeResourceUrl {
    return this.sanitizer.bypassSecurityTrustResourceUrl(`https://www.youtube.com/embed/${url}`);
  }
}
